@import url(https://fonts.googleapis.com/css2?family=Zen+Dots&display=swap);
body {
    background: #12965f;
    width: 1200px;
    height: 800px;
}

.header {
    
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%, 0%);
            transform: translate(-50%, 0%);
}
.header h2 {
    color: brown;
    font-size: 50px;
    font-family: 'Zen Dots';
    font-weight: 600;
}


.header .userName input {
    width: 400px;
    height: 50px;
    border-radius: 5px;
    font-size: 15px;
    font-weight: 600;
    margin-right: 10px;

}

.header .userName button {
    width: 150px;
    height: 58px;
    border-radius: 5px;
    background: #7be3b7;
    margin-left: 10px;
}

.header .userName button:hover {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.menu {

    position: absolute;
    top: 30%;
    left: 50%;
    -webkit-transform: translate(-50%, 0%);
            transform: translate(-50%, 0%);
}

.menu button {
    margin: 20px;
    width: 400px;
    height: 100px;
    border-radius: 10px;
    font-size: 20px;
    font-weight: 600;
    color: #fff;
    background: #e6ba6e;
}

.menu button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
